@keyframes app-bg {
  0%{background-position:center;}
  12%{background-position: 90% 75%;}
  25%{background-position: 50% 100%;}
  37%{background-position: 10% 75%;}
  50%{background-position: center;}
  62%{background-position: 90% 25%;}
  75%{background-position: 50% 0;}
  87%{background-position: 10% 25%;}
  100%{background-position: center;}
}
@-moz-keyframes app-bg {
  0%{background-position:center;}
  12%{background-position: 90% 75%;}
  25%{background-position: 50% 100%;}
  37%{background-position: 10% 75%;}
  50%{background-position: center;}
  62%{background-position: 90% 25%;}
  75%{background-position: 50% 0;}
  87%{background-position: 10% 25%;}
  100%{background-position: center;}
}
@-webkit-keyframes app-bg {
  0%{background-position:center;}
  12%{background-position: 90% 75%;}
  25%{background-position: 50% 100%;}
  37%{background-position: 10% 75%;}
  50%{background-position: center;}
  62%{background-position: 90% 25%;}
  75%{background-position: 50% 0;}
  87%{background-position: 10% 25%;}
  100%{background-position: center;}
}

.App {
  display: flex;
  position: sticky;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  background: radial-gradient(circle, #e48ec9, var(--main-color), #8ee4bd);
  background-size: 200% 200%;
  /* background-position: 90% 85%; */
  /* animation: name duration timing-function delay iteration-count direction fill-mode; */
  animation: app-bg 60s forwards infinite;
  -moz-animation: app-bg 60s forwards infinite;
  -webkit-animation: app-bg 60s forwards infinite;
}

.App-logo {
  /* height: 40vmin; */
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.section {
  padding-top: 2.5em;
}

.App-header {
  /* min-height: 60%; */
  /* height: fit-content; */
  padding-bottom: 3em;
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 3.5em;
  color: white;
}

.container {
  color: white;
  margin: 0 auto;
  /* font-size: 2vw; */
  display: inherit;
  position: relative;
  align-items: center;
  margin: 0 10vw;
}

.App-link {
  color: #61dafb;
}

.right-header {
  display: inline-block;
  /* flex-direction: column; */
  /* height: max-content; */
  width: 50vw;
  /* height: 50vw; */
  margin-left: 2vw;
}

.left-header {
  display: inline-block;
  align-items: center;
  height: max-content;
  /* height: 40vw; */
  width: 35vw;
}

h1.title, p.title {
  font-size: 1.5em;
  margin: 0;
}

p.subtext, li.subtext {
  color: white;
  font-size: .75em;
  margin: 14px 0;
}

h5.subtitle {
  font-size: .75em;
  margin: 0;
}

h3.subtitle {
  white-space: nowrap;
  overflow: hidden;
  font-size: 1.5em;
}

#recruitment {
  display: block;
}

.recruit-container {
  display: block;
  float: left;
  text-align: center;
}

.recruitment-status {
  display: block;
  color: white;
  font-size: 1em;
  text-align: left;
}

.recruitment-text {
  display: inline-flex;
  font-size: 2vw;
  margin-bottom: .5em;
}

.recruitment-light {
  /* display: flex; */
  font-size: 2vw;
  margin: 0 1vmin 1vmin 0;
  vertical-align:text-bottom;
  animation: light-flash 1s ease 1s infinite;
  -webkit-animation: light-flash 1s ease 1s infinite;
  color: var(--light-color-dim-l);
}

.no-piano-text {
  display: none;
}

#nav-container {
  bottom: -2em;
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.navbar {
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: center;
  padding: 1em;
  width: 30em;
  background: white;
  border-radius: 10em;
  z-index: 4;
  justify-content: center;
  /* min-height: 3.25rem; */
}

.nav-item {
  display: flex;
  /* padding: 1.5vmin 3vmin 1.5vmin 3vmin; */
  padding: 1em;
  margin: 0 1em;
  background-color: #e48ec9;
  color: white;
  border-radius: 50px;
  font-size: .65em;
  align-items: center;
  justify-content: center;
  /* min-height: 40px; */
  min-width: 90px;
}

#about-me {
  padding: 50px 0 50px 0;
  /* background: radial-gradient(var(--main-color), #8ee4bd, #e48ec9); */
  display: flex;
  height: max-content;
  width: 100%;
}

#about-me .container { 
  font-size: 3.5em;
}

#about-me a.suzie {
  position: relative;
}

#about-me a.suzie span {
  position: absolute;
  display: none;
  z-index: 99;
}

#about-me a:hover.suzie span {
  display: block;
}

#cutest-cat-ever {
  border-radius: 10px;
}

.left-about {
  height: max-content;
  /* padding: 0 5vw; */
  text-align: left;
}

.me {
  max-height: 500px;
  max-width: 500px;
  width: 100%;
  /* height: 100px; */
  object-fit: cover;
  border-radius: 10px;
  /* object-position: -1270px 65%; */
}

.right-about {
  height: max-content;
  padding-top: 2em;
  text-align: left;
  /* font-size: 24px; */
}

li.main-text {
  margin: 10px 0;
}

p.about-subtitle {
  margin: 50px 0 0 50px !important;
}

#projects {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: max-content;
  /* font-size: 3.5em; */
}

#projects > .container > h3 {
  margin-bottom: 0;
}

.instructions {
  font-size: .4em;
  margin-bottom: 2em;
}

#projects > .container {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  /* margin-top: 2em; */
  font-size: 3.5em;
}

#projects > .container > .container {
  margin: 0;
  align-items: center;
}

.grid-container {
  width: 100%;
  padding: .5em 0;
  display: grid;
  grid-template-columns: repeat(2, 50%);
}

.project-container {
  margin: 0 1em;
  height: max-content;
}

.project-pic {
  width: 100%;
  height: 70%;
  object-fit: cover;
  border-radius: 10px;
}

.project-pic.filler {
  background-color: black;
  margin: 0 auto;
}

#projects p.subtext {
  font-size: .75em;
}

#projects p.proj-desc {
  font-size: .5em;
}

#resume {
  /* background:
    linear-gradient(60deg, #8ee4bd, transparent 60%),
    linear-gradient(300deg, #5d9d80 0, transparent 60%),
    radial-gradient(var(--main-color), transparent 175%); */
  display: flex;
  align-items: center;
  justify-content: center;
  height: max-content;
  width: 100%;
  padding-bottom: 5em;
  width: 100%;
}

#resume-pdf {
  margin: auto;
}

#responsive-canvas {
  width: 100%!important;
  height: 75%!important;
}

h4.resume-title {
  font-size: .75em;
  margin: 14px 0;
}

#resume .container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  margin: 0 10vw;
  font-size: 3.5em;
}

.resume-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.resume-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.resume-item > ul {
  margin: 0;
}

.main-text {
  color: white;
  text-align: start;
}

p.main-text, li.main-text {
  color: white;
  font-size: .75em;
  line-height: 1em;
  margin: 0 2em;
}

/* li.main-text {
  font-size: .5em;
  margin: 0 2em
} */

