@media only screen and (max-width: 800px) {
  .App-header {
    /* height: 20%; */
    /* min-height: 0; */
    flex-direction: column;
    align-items: center;
    font-size: 3vw;
  }

  .right-header {
    /* height: 40vh;  */
    width: 80%;
  }
  
  .left-header {
    display: flex;
    flex-direction: column;
    height: max-content; 
    width: 70vw;
  }

  h3.subtitle, p.subtitle {
    font-size: 2em;
    margin: .3em 0;
  }

  h4.resume-title {
    margin: 2px 0;
  }

  .recruitment-status {
    display: block;
    color: white;
    font-size: 3.5vmin;
    text-align: left;
  }
  
  .recruitment-text {
    font-size: 5vmin;
    margin-bottom: .5em;
  }

  h1.title {
    font-size: 3em;
  }

  .subtitle {
    font-size: 1em;
    margin: 0;
  }
  
  .recruitment-light {
    font-size: 5vmin;
    margin: 0 1vmin 1vmin 0;
  }

  #piano {
    /* max-height: 20vh; */
    display: none;
  }

  .piano-text {
    display: none;
  }

  .no-piano-text {
    display: inherit;
    font-size: .75em !important;
  }

  .grid-container {
    grid-template-columns: none;
    /* grid-template-rows: repeat(2, 50%); */
  }

  .project-container {
    height: max-content;
    padding: .5em 0;
  }

  .project-pic {
    width: 80%;
    height: 80%;
  }

  #about-me .container {
    flex-direction: column;
  }

  .right-about {
    padding: 0
  }

  /* #resume .container, #about-me .container, #projects .container {
    font-size: 5vw; 
  } */
}

@media only screen and (max-width: 400px) {
  .App-header {
    /* height: 60vh; */
    min-height: 0;
    flex-direction: column;
    justify-content: flex-start;
    font-size: 5vw;
  }

  .recruitment-status {
    display: block;
    color: white;
    font-size: 3.5vmin;
    text-align: left;
  }
  
  .recruitment-text {
    font-size: 5vmin;
    margin-bottom: .5em;
  }

  h1.title {
    font-size: 2em;
  }

  p.subtext, li.subtext, p.main-text, li.main-text {
    font-size: .75em;
    margin: 0;
  }
  
  .recruitment-light {
    font-size: 5vmin;
    margin: 0 1vmin 1vmin 0;
  }

  .right-header {
    display: block;
    /* height: 30vh;  */
    width: 80%;
    max-height: 250px;
  }
  
  .left-header {
    display: block;
    /* height: max-content;  */
    width: 70vw;
  }

  .nav-item {
    min-width: 15vw;
  }

  #about-me {
    padding: 10em 0;
  }

  #about-me > .container, #projects > .container, #resume > .container {
    font-size: 5em;
  }

  /* .container {
    font-size: 6em;
  } */

  #resume .container, #about-me .container, #projects .container {
    flex-direction: column;
  }

  p.about-subtitle {
    margin: 0 !important;
  }

